const menus = [
    {id: '0', label: '控制面板', path: '/', icon: 'fa fa-home'},
    {
        id: 'userInfo',
        icon: 'fa fa-user-circle-o',
        label: '用户信息',
        submenu: [
            {id:'user-table',name:'user-table',label:'用户列表',icon:'fa fa-address-book-o'},
            {id:'user-table-before-tabs',name:'user-table-before-tabs',label:'过期用户',icon:'fa fa-user-times'},
            {id:'custom-table',name:'custom-table',label:'客户信息',icon:'fa fa-user-md'},
            {id:'editor',name:'editor',label:'富文本编辑器',icon:'fa fa-file-word-o'}
        ]
    },
    {
        id: 'chartInfo',
        icon: 'fa fa-bar-chart',
        label: '图表分析',
        submenu: [
            {id:'basic-chart',name:'basic-chart',label:'基本图表',icon:'fa fa-bar-chart'},
            {id:'other-chart',name:'other-chart',label:'其他图表',icon:'fa fa-line-chart'},
            {id:'word-cloud',name:'word-cloud',label:'字符云',icon:'fa fa-cloud'}
        ]
    },
    {
        id:'highLight',name:'high-light',label:'代码高亮显示', icon:'fa fa-code',
    },
    {
        id: 'components',
        icon: 'fa fa-microchip',
        label: 'Components 组件',
        submenu: [
            ['Form 表单', [
                // {id: 'f4', label: 'Radio 单选框', icon: 'fa fa-dot-circle-o'},
                {id: 'f6', name: 'c-keyboard', label: '虚拟键盘', icon: 'fa fa-keyboard-o'}
            ]],
        ]
    },
    {
        id: 'pages',
        icon: 'fa fa-pied-piper',
        label: 'Pages 页面',
        submenu: [
            {id: 'p-data-table', name: 'p-data-table', label: 'Table 表格数据'},
            {id: 'p1', name: 'login', label: 'Login 登录'},
            {id: 'p2', name: 'p-login', label: 'Login 登录2'},
            {id: 'p3', name: 'p-register', label: 'Register 注册'},
            {id: 'p4', path: '/404', label: '404'},
            {id: 'p5', path: '/500', label: '500'}
        ]
    },
    {
        id: 'examples',
        icon: 'fa fa-paper-plane',
        label: 'Examples',
        submenu: [
            {id: 'e1', path: '/500', label: 'Vuex 例子'}
        ]
    },
    {
        id: 'version',
        icon: 'fa fa-meetup',
        label: '应用'
    }
]
export default menus



